import Endpoint from '@/services/Endpoint'

export default {
  dcTypes (payload) {
    return Endpoint().get('/s1/tradeFinance/dcType', {
      params: payload
    })
  },
  createDcType (payload) {
    return Endpoint().post('/s1/tradeFinance/dcType', payload)
  },
  modifyDcType (payload) {
    return Endpoint().patch('/s1/tradeFinance/dcType', payload)
  }
}
